 "use client";
import { useState } from "react";
import {
  Menu,
  X,
  Home,
  FileText,
  BarChart2,
  Settings,
  LogOut,
  User,
} from "lucide-react";

export default function Layout() {
  const [open, setOpen] = useState(false);
  const [active, setActive] = useState("Dashboard");

  const toggleSidebar = () => setOpen(!open);

  const menuItems = [
    { name: "Dashboard", icon: Home },
    { name: "Reports", icon: BarChart2 },
    { name: "Documents", icon: FileText },
    { name: "Settings", icon: Settings },
  ];

  return (
    <div className="relative min-h-screen flex flex-col">
      {/* Header */}
      <header className="flex items-center justify-between bg-white px-4 py-3 shadow-md">
        <h1 className="text-xl font-semibold text-gray-800">My App</h1>
        <button
          onClick={toggleSidebar}
          className="text-gray-700 hover:text-[#009333]"
        >
          {open ? <X size={28} /> : <Menu size={28} />}
        </button>
      </header>

      {/* Sidebar */}
      <div
        className={`fixed top-0 left-0 h-full w-72 bg-white shadow-2xl transform transition-transform duration-300 z-50 ${
          open ? "translate-x-0" : "-translate-x-full"
        }`}
      >
        {/* User Profile */}
        <div className="flex items-center gap-3 p-4 border-b border-gray-200">
          <div className="bg-[#009333] text-white p-3 rounded-full">
            <User size={24} />
          </div>
          <div>
            <p className="text-gray-800 font-semibold">Test User</p>
            <p className="text-sm text-gray-500">user@example.com</p>
          </div>
        </div>

        {/* Menu */}
        <nav className="p-4 flex flex-col gap-2">
          {menuItems.map((item, idx) => {
            const Icon = item.icon;
            const isActive = active === item.name;
            return (
              <button
                key={idx}
                onClick={() => setActive(item.name)}
                className={`flex items-center gap-3 px-4 py-2 rounded-xl transition-colors ${
                  isActive
                    ? "bg-[#009333] text-white"
                    : "text-gray-700 hover:bg-[#009333] hover:text-white"
                }`}
              >
                <Icon size={20} />
                {item.name}
              </button>
            );
          })}
        </nav>

        {/* Sign Out */}
        <div className="absolute bottom-6 w-full px-4">
          <button className="flex items-center justify-center gap-2 w-full py-3 rounded-xl font-semibold text-white bg-[#009333] hover:bg-green-700 transition-colors">
            <LogOut size={20} />
            Sign Out
          </button>
        </div>
      </div>

      {/* Backdrop */}
      {open && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 z-40"
          onClick={toggleSidebar}
        ></div>
      )}

      {/* Main content */}
      <main className="flex-1 p-6">
        <h2 className="text-2xl font-bold text-gray-800">{active}</h2>
        <p className="text-gray-600 mt-2">
          This is the <span className="font-semibold">{active}</span> page.
          Click the menu to switch sections.
        </p>
      </main>
    </div>
  );
}
